//
const getKaoyan_date = (date) => {
	return Date.parse(new Date(date + '-11-08')) - Date.parse(new Date());
}

//
const getSeasonTime = (month) => {
	return (month / 3) + 1;
}

//yyyy-MM-dd
const getNowFormatDate = () => {
	let date = new Date(),
		year = date.getFullYear(), //(4)
		month = date.getMonth() + 1, //(0-11,01)
		strDate = date.getDate() // (1-31)
	if (month < 10) month = `0${month}` // 0
	if (strDate < 10) strDate = `0${strDate}` // 0

	return `${year}-${month}-${strDate}`;
}

//
const getDay = (date) => {
	let re = new Date(date).getDay();
	return re === 0 ? 7 : re;
}

//
const getWeek = (date) => {
	let before = new Date(getApp().globalData.start_date).getTime();
	let after = new Date(date).getTime();
	return parseInt((after - before) / (1000 * 60 * 60 * 24 * 7)) + 1;
}

module.exports = {
	getKaoyan_date,
	getSeasonTime,
	getDay,
	getWeek,
	getNowFormatDate
};
